import { Controller } from '@ali/4ever-cangjie';
import { SZ_ADJUST, SZ_SET } from '../actions';
import setSize from '../commands/setSize';
import adjustSz from '../commands/adjustSz';

export default function onAction(action, controller: Controller, next) {
  const { type, payload } = action;
  if (type === SZ_SET) {
    const { sz } = payload;
    setSize(controller, sz);
  }
  if (type === SZ_ADJUST) {
    const { isIncreasingSz } = payload;
    adjustSz(controller, isIncreasingSz);
  }
  return next();
}
